'use client';

import { useState, useCallback } from 'react';

export interface VpnServer {
  id: string;
  name: string;
  country: string;
  city: string;
  ping: number;
  load: number;
}

export interface ConnectionHistory {
  id: string;
  server: string;
  connectedAt: Date;
  disconnectedAt?: Date;
  duration?: number;
}

export const useVpn = () => {
  const [isConnected, setIsConnected] = useState(false);
  const [isConnecting, setIsConnecting] = useState(false);
  const [selectedServer, setSelectedServer] = useState<VpnServer | null>(null);
  const [connectionHistory, setConnectionHistory] = useState<ConnectionHistory[]>([]);
  const [error, setError] = useState<string | null>(null);
  const [connectedAt, setConnectedAt] = useState<Date | null>(null);

  // Danh sách máy chủ VPN mẫu (mô phỏng)
  const availableServers: VpnServer[] = [
    { id: '1', name: 'Hà Nội', country: 'Việt Nam', city: 'Hà Nội', ping: 15, load: 45 },
    { id: '2', name: 'TP.HCM', country: 'Việt Nam', city: 'TP.HCM', ping: 12, load: 60 },
    { id: '3', name: 'Singapore', country: 'Singapore', city: 'Singapore', ping: 25, load: 30 },
    { id: '4', name: 'Tokyo', country: 'Nhật Bản', city: 'Tokyo', ping: 45, load: 55 },
    { id: '5', name: 'Seoul', country: 'Hàn Quốc', city: 'Seoul', ping: 40, load: 35 },
    { id: '6', name: 'Hong Kong', country: 'Hong Kong', city: 'Hong Kong', ping: 30, load: 70 },
  ];

  const selectServer = useCallback((server: VpnServer) => {
    if (isConnecting || isConnected) {
      setError('Vui lòng ngắt kết nối trước khi chọn máy chủ khác');
      return;
    }
    setSelectedServer(server);
    setError(null);
  }, [isConnecting, isConnected]);

  const connectVpn = useCallback(async () => {
    if (!selectedServer) {
      setError('Vui lòng chọn máy chủ VPN trước khi kết nối');
      return;
    }

    if (isConnected || isConnecting) {
      return;
    }

    setIsConnecting(true);
    setError(null);

    try {
      // Mô phỏng thời gian kết nối (1-3 giây)
      const connectionTime = Math.random() * 2000 + 1000;
      
      await new Promise((resolve, reject) => {
        setTimeout(() => {
          // Mô phỏng khả năng kết nối thất bại (10% cơ hội)
          if (Math.random() < 0.1) {
            reject(new Error('Không thể kết nối đến máy chủ VPN'));
          } else {
            resolve(true);
          }
        }, connectionTime);
      });

      const now = new Date();
      setIsConnected(true);
      setConnectedAt(now);
      
      // Thêm vào lịch sử kết nối
      const newConnection: ConnectionHistory = {
        id: Date.now().toString(),
        server: `${selectedServer.name}, ${selectedServer.country}`,
        connectedAt: now,
      };
      
      setConnectionHistory(prev => [newConnection, ...prev.slice(0, 9)]); // Giữ 10 kết nối gần nhất
      
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Lỗi kết nối không xác định');
    } finally {
      setIsConnecting(false);
    }
  }, [selectedServer, isConnected, isConnecting]);

  const disconnectVpn = useCallback(() => {
    if (!isConnected) {
      return;
    }

    setIsConnected(false);
    setIsConnecting(false);
    setError(null);

    // Cập nhật lịch sử kết nối
    if (connectedAt) {
      const now = new Date();
      const duration = Math.floor((now.getTime() - connectedAt.getTime()) / 1000);
      
      setConnectionHistory(prev => 
        prev.map((conn, index) => 
          index === 0 ? { ...conn, disconnectedAt: now, duration } : conn
        )
      );
    }
    
    setConnectedAt(null);
  }, [isConnected, connectedAt]);

  const clearError = useCallback(() => {
    setError(null);
  }, []);

  const getConnectionDuration = useCallback(() => {
    if (!isConnected || !connectedAt) return 0;
    return Math.floor((new Date().getTime() - connectedAt.getTime()) / 1000);
  }, [isConnected, connectedAt]);

  return {
    // States
    isConnected,
    isConnecting,
    selectedServer,
    connectionHistory,
    error,
    availableServers,
    connectedAt,
    
    // Actions
    selectServer,
    connectVpn,
    disconnectVpn,
    clearError,
    getConnectionDuration,
  };
};
